@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  background: #f39f0dc5;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #f39e0d, #ffcf3fd5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
 }

code {
  font-family:'Roboto Slab';
}
div:focus{
  outline: none !important;
}

.header{
    margin-bottom: 40px;
    color: #fff;
   
    border-radius: 0px;
    background: #1f8e64;
    box-shadow:  5px 5px 10px #0c3928,
                 -5px -5px 10px #32e3a0;
}
.bienvenido{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    min-height: 90vh;
    font-family:'Roboto Slab';
    font-size: 25px;
    color: cornsilk;
}
.bienvenido h2:hover{
    color: crimson;
    cursor: pointer;
}
.imagenLogo{
    position: absolute;
    z-index: -22;
    max-width: 100%;

}
.tablero{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    grid-gap: 5px;
    gap: 5px;
    max-width: 700px;
    width: 100%;
    margin: auto;
}

.carta{
    display: -webkit-flex;
    display: flex;
    width: 125px;
    height: 125px;
  
}
.portada{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 30px;
    border: solid 1px;
    border-radius: 10px;
    width: 125px;
    height: 125px;
    background-color: wheat;
}
.contenido{
    border: solid 1px;
    border-radius: 10px;
    width: 125px;
    height: 125px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: turquoise;
}
.ReactCardFlip{
    width: 100%;
    height: 100%;
}

.reiniciar:hover{
    border-bottom: 1px solid salmon;
    color: salmon;
    cursor: pointer;
}
@media all and (max-width:470px) {
    .carta{
    
        width: 75px;
        height: 75px;
      
    }
    .portada{
        
        width: 75px;
        height: 75px;
      
    }
    .contenido{
     
        width: 75px;
        height: 75px;
       
    }
}

