@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@800&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  background: #f39f0dc5;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #f39e0d, #ffcf3fb6);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #f39e0d, #ffcf3fd5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
 }

code {
  font-family:'Roboto Slab';
}
div:focus{
  outline: none !important;
}
